export default {
	data() {
		return {
			list: [], // 任务列表
			active: false, // 激活创建状态
			value: '', // 输入框
			activeIndex: 0, // 状态栏
			text: '全部'
		};
	},
	onLoad() {},
	computed: {
		listData() {
			let list = JSON.parse(JSON.stringify(this.list)); // 深拷贝
			let newList = [];
			// 全部
			if (this.activeIndex === 0) {
				this.text = '全部';
				return list;
			}
			// 待办
			if (this.activeIndex === 1) {
				this.text = '待办';
				// checked = false
				list.forEach(item => {
					if (!item.checked) {
						newList.push(item);
					}
				});
				return newList;
			}
			// 已完成
			if (this.activeIndex === 2) {
				this.text = '已完成';
				// checked = true
				list.forEach(item => {
					if (item.checked) {
						newList.push(item);
					}
				});
				return newList;
			}
			return [];
		}
	},
	methods: {
		//打开输入框
		create() {
			this.active = !this.active;
		},
		// 创建任务
		add() {
			if (this.value === '') {
				uni.showToast({
					title: '请输入内容',
					icon: 'none'
				});
				return;
			}
			this.list.unshift({
				content: this.value,
				id: 'id' + new Date().getTime(),
				checked: false
			});
			this.value = '';
			this.active = false;
		},
		// 完成任务
		finish(id) {
			let index = this.list.findIndex(item => item.id === id);
			// console.log(this.list[index]);
			this.list[index].checked = !this.list[index].checked;
		},
		tab(index) {
			this.activeIndex = index;
		}
	}
};
